import React from "react";
import "./App.css";
import Banner from "./components/Banner";
import Content from "./components/Content";
import confetti from "canvas-confetti";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";

var count = 500;
var defaults = {
  origin: { y: 1 }
};

function fire(particleRatio, opts) {
  confetti(
    Object.assign({}, defaults, opts, {
      particleCount: Math.floor(count * particleRatio)
    })
  );
}

function fireConfetti() {
  fire(0.25, {
    spread: 26,
    startVelocity: 55
  });
  fire(0.2, {
    spread: 60
  });
  fire(0.35, {
    spread: 100,
    decay: 0.91
  });
  fire(0.1, {
    spread: 200,
    startVelocity: 25,
    decay: 0.92
  });
  fire(0.1, {
    spread: 300,
    startVelocity: 45
  });
}

function onMagicClick() {
  var random = Math.round(Math.random() * 10);

  switch (random) {
    case 0:
      fireConfetti();

    case 1:
      toast("Oh hi there, üëÄ", {
        autoClose: 4000
      });

    default:
    // fireConfetti();
  }
  /* 
  
  show bowser at bottom for brief time
  turn mouse into mouse
  
  
  */
}

function App() {
  return (
    <div className="App">
      <Banner onMagicClick={onMagicClick} />
      <ToastContainer />
      <Content />
    </div>
  );
}

export default App;
